import React, { Component } from 'react'
import { Link } from 'react-router'
import { connect } from 'react-redux'
import CSSModules from 'react-css-modules-simply'

import styles from './index.css'
import comAction from '../../actions/common'

class Profile extends Component{

  render() {
    return (
      <div style="profile">
        <div style="left-panel">
          <div style="grey"></div>
          <div style="headImg">
            <img src="../../assets/images/profileHeadImg.png"/>
          </div>
          <div style="username">
            jettang
          </div>
          <div style="lv">
            Lv.17
          </div>
          <div style="location-icon"></div>
          <div style="location-name">广州</div>
          <div style="normal-position">
            <div style="signature">
              <div style="signature-lbl">最怕脑子不好使</div>
              <div style="signature-icon"></div>
            </div>
            <div style="gold-fav">
              <ul>
                <li style="gold-icon"></li>
                <li style="gold-num">
                  63
                </li>
                <li style="vertical-line"></li>
                <li style="fav-icon"></li>
                <li style="fav-num">
                    238
                </li>
              </ul>
            </div>
            <div style="circles">
              <ul>
                <li style="weibo"></li>
                <li style="github"></li>
                <li style="zhihu"></li>
                <li style="twitter"></li>
              </ul>
            </div>
            <div style="blog">
              www.jettangs.com
            </div>
            <div style="follow">
              <ul style="content">
                <li style="followers">
                  <div style="num">14</div>
                  <div style="lbl">Followers</div>
                </li>
                <li style="following">
                  <div style="num">8</div>
                  <div style="lbl">Following</div>
                </li>
                <li style="postings">
                  <div style="num">26</div>
                  <div style="lbl">Postings</div>
                </li>
              </ul>
            </div>
          </div>
        </div>
        <div style="right-panel">
          {this.props.children}
        </div>
      </div>
    )
  }
}

const mapStateToProps = (state,prop)=>{
    return {
       usrIsSgi: state.common.usrIsSgi
    }
}

const mapDispatchToProps = {
    setUsrIsSgi : (val) => comAction.setUsrIsSgi(val),
    dispSignBox: (bool) => comAction.dispSignBox(bool),
}

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(CSSModules(Profile,styles))


